.game-container{
    background-color: pink;
    height: 100vh;
    padding: 1px 0 0 0;
    margin: -1px auto 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.square{
    height: 100px;
    width: 100px;
    background-color: black;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.square-selected{
    height: 100px;
    width: 100px;
    background-color: white;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.selection-container{
    display: flex;
}

.circle{
    height: 10px;
    width: 10px;
    border-radius:5px;
    background-color: black;
    margin-top: 25px;
    margin-bottom: 25px;
}

.object-listing{
    width:300px;
    height: 50px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: black;
    color:white;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.new-object-form{
    width: 320px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.object-form-item{
    max-width: 320px;
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 10px;
    margin-bottom: 0px;
    width: 100%;
}

input.object-form-item{
    width: 310px;
    height: 36px;
    margin: 0;
    padding: 5px
}

h3.object-form-item{
    text-align: center;
    margin-bottom: 10px;
}



